
import { defineComponent } from "@vue/runtime-core";

export default defineComponent({
    name: 'ComponentPicker',
    data: () => ({
        table: "Table",
        chartPie: "PieChart",
        chartLine: "LineChart",
        chartArea: "AreaChart",
        chartBar: "BarChart"
    }),
    emits: ["pickComponent"],
    methods: {
        pickComponent(layout: string){
            this.$emit('pickComponent', {
                layout: layout
            })
        }

    }
})
